<template>
   <div class="yinhangka">
       <Bank />

       <!-- 密码输入框 -->
        <div class="yinhangka02" v-if="passStatus">
            <p> 验证支付密码 </p>
            <van-password-input
            :value="pass"
            info="密码为 6 位数字"
            :focused="showKeyboard"
            @focus="showKeyboard = true"
            />
        </div>

       <div class="yinhangka01" v-else>
            <p> * 建议绑定本人工、农、建行的银行卡, 可提现实时到账。 </p>
            <van-field v-model="name" label="持卡人姓名" placeholder="请输入持卡人姓名" />
            <van-field v-model="cardNumber" type="digit" label="银行卡号" placeholder="请输入银行卡号" />
            <van-field v-model="bankName" label="选择银行" readonly placeholder="" @click="showPicker = true" />
            <van-field v-model="bankHu" label="开户行" placeholder="请输入开户行" />

            <p style="color:#333;"> * 请核对账户信息，一经绑定将无法更改。 </p>
            <van-button type="danger" block round @click="sub"> 确定绑定 </van-button>
        </div>
        

       <van-popup v-model="showPicker" position="bottom">
        <van-picker
            show-toolbar
            :columns="columns"
            @cancel="showPicker = false"
            @confirm="onConfirm"
        />
        </van-popup>

        <!-- 数字键盘 -->
<van-number-keyboard
  :show="showKeyboard"
  @input="onInput"
  @delete="onDelete"
  @blur="showKeyboard = false"
/>
        
   </div>
</template>
<script>
import Bank from '@/components/NavBar/bank.vue'
   export default { 
      name: "yinhangka",
      components: {Bank},
      data() {
         return {
            //  密码框状态
             passStatus: true,
            // 银行信息
             name:'',
             cardNumber:'',
             bankName:'请选择银行',
             bankHu:'',

            // 银行选择框
            showPicker: false,
             columns: [
                 '中国农业银行', 
                 '招商银行', 
                 '中国建设银行', 
                 '民生银行', 
                 '中国邮政储蓄银行',
                 '中国工商银行',
                 '中国银行',
                 '交通银行',
                 '中信银行',
                 '光大银行',
                 '浙江农村信用社',
                 '华夏银行',
                 '广发银行',
                 '上海浦东发展银行',
                 '兴业银行',
                 '浙商银行',
                 '渤海银行',
                 '平安银行',
                 '上海银行',
                 '厦门银行',
                 '北京银行',
                 '吉林银行',
                 '宁波银行',
                 '广州银行',
                 '齐商银行',
                 '锦州银行',
                 '重庆银行',
                 '哈尔滨银行',
                 '东莞农村商业银行',
                 '广东农村信用社联合社银行',
                 '贵州农信',
                 '河南省农村信用社',
                 '江苏农村商业银行',
                 '广西农村信用社',
                 '甘肃省农村信用社',
                 '云南省农村信用社',
                 '青岛银行',
                 '黑龙江省农村信用社联合社',
                 '湖北省农村信用社',
                 '湖南省农村信用社联合社'
                 ],

                // 密码键盘
                 pass: '',
                showKeyboard: false
         }
      },
      computed: {},
      mounted: function() {},
      methods: {
            onConfirm(value,index) {
              this.showPicker = false;
              this.bankName = value;
              this.$toast(`当前值：${value}, 当前索引：${index}`);
            },

            onInput(key) {
                this.pass = (this.pass + key).slice(0, 6);
                this.$toast(this.pass.length)
                if(this.pass.length == 6){
                    this.showKeyboard = false;
                    this.passStatus = false;
                }
            },
            onDelete() {
                this.pass = this.pass.slice(0, this.pass.length - 1);
            },
            sub(){
                this.$toast.success(`绑卡成功！`);
                this.$router.go(-1);
            }
        }
   }
</script>
<style lang="scss">
.yinhangka{
    text-align: left;
    >.yinhangka01{
            >p{
        color: #444;
        margin: 0;
        line-height: 40px;
        padding-left: 20px;
        font-size: 13px;
    }
    }
    >.yinhangka02{
        background-color: #f0f0f0;
        margin-top: 50%;
        padding: 40px 0;
        >p{
            text-align: center;
        }
    }

}
